body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body-cont {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
aside {
  background: #1f2018 url(/images/restaruant_bg.jpg);
  background-size: cover;
  background-position: 50%;
  width: 420px;
  height: 100vh;
  overflow: hidden;
}
main{
  -webkit-flex: 1 1;
          flex: 1 1;
  margin:16px;
}
form input[type=text]{
  outline: none;
  font-size: 18px;
  padding: 16px;
  margin-right:8px;
}
button{
  padding: 16px;
  font-size: 18px;
  cursor: pointer;
}
.searchForm{
  height: 70px;
  overflow: hidden;
}
.results{
  height: calc(100vh - 200px);
  overflow: auto;
  max-width: 850px;
  min-width: 500px;
}
.resultHeader,.resultElement{
  display: grid;
  grid-template-columns: 4fr 2fr 1fr;
  height: 50px;
  overflow: hidden;
  max-width:800px;
}
.resultHeader h2{
  font-size: 16px;
  font-weight: 400;
}
.resultElement{
  border-bottom: 1px solid #CACACA;
}
.resultElement h3{
  font-size: 14px;
  font-weight: 400;
}
